<template>
    <div class="promotion-settings" v-loading="loading">
        <div class="no-data" v-if="extendList.length<1">
            {{ $L('暂无数据') }}
        </div>
        <div v-else class="content-view">
            <div class="promotion-left">
                <div class="system-header">
                    {{$L('模板')}}
                </div>
                <div class="item-box">
                    <ul class="ul">
                        <li v-for="(item,key) in extendList" :key="key" class="li">
                            <div class="model-ul">
                                <div class="num">{{ key+1 }}</div>
                                <div class="item img-item" v-on:click="getData(item.id)">
                                    <diy-image :src="item.img"></diy-image>
                                </div>
                                <div class="selected" :class="{active:item.id === promotion.id}"><span
                                        class="el-icon-check"></span></div>
                            </div>
                        </li>
                    </ul>
                </div>
            </div>
            <div class="promotion-right">
                <div class="promotion-tit">
                    {{ $L('海报设置') }}
                </div>
                <div class="info-main">
                    <div class="info-left" ref="showBox">
                        <share-img :promotion="promotion"></share-img>
                    </div>
                    <div class="info-right">
                        <el-form ref="form" label-width="110px" size="mini">
                            <el-form-item :label="$L('默认头像')+'：'">
                                <div class="poster-resource">
                                    <el-radio-group v-model="promotion.headType">
                                        <el-radio label="1">{{ $L('使用会员头像') }}</el-radio>
                                        <el-radio label="2">{{ $L('使用分销商店铺LOGO') }}</el-radio>
                                        <el-radio label="0">{{ $L('不使用头像') }}</el-radio>
                                    </el-radio-group>
                                </div>
                            </el-form-item>
                            <el-form-item :label="$L('页面背景')+'：'">
                                <div class="reset-btn">
                                    <el-checkbox v-model="promotion.isShowBg" true-label="1"
                                                 false-label="0"></el-checkbox>
                                </div>
                                <diy-image class="bg-img" :src="promotion.bgImage" :error-text="$L('点击上传')"></diy-image>
                                <div class="load-right">
                                    <div class="reset-btn">
                                        <label class="label">
                                            <el-button size="small">{{ $L('上传图片') }}</el-button>
                                            <input class="file-input" type="file" name="CommentsPic"
                                                   :placeholder="$L('上传图片')" multiple
                                                   accept="image/png,image/jpeg,image/jpg" @change="upImgFunc"/>
                                        </label>
                                        <el-button size="small" @click="defaultImg">{{ $L('默认背景') }}</el-button>
                                    </div>
                                    <div class="poster-color reset-btn">
                                        <el-color-picker v-model="promotion.bgColor"></el-color-picker>
                                    </div>
                                    <div class="tips">{{ $L('建议尺寸:375x560px，小于2M，PNG、JPG格式') }}</div>
                                </div>
                            </el-form-item>
                            <el-form-item :label="$L(' 大标题')+'：'">
                                <el-checkbox v-model="promotion.isTitle" true-label="1" false-label="0"></el-checkbox>
                                <div class="poster-tit">
                                    <el-input v-model="promotion.extTitle" width="300"></el-input>
                                </div>
                                <div class="poster-color">
                                    <el-color-picker v-model="promotion.extTColor"></el-color-picker>
                                </div>
                                <div class="poster-size">
                                    <el-select v-model="promotion.extTSize">
                                        <el-option
                                                v-for="item in options"
                                                :key="item.value"
                                                :label="item.label"
                                                :value="item.value">
                                        </el-option>
                                    </el-select>
                                </div>
                                <div class="poster-fam">
                                    <el-select v-model="promotion.extTFont">
                                        <el-option
                                                v-for="item in optionsFam"
                                                :key="item.value"
                                                :label="item.label"
                                                :value="item.value">
                                        </el-option>
                                    </el-select>
                                </div>
                            </el-form-item>
                            <el-form-item :label="$L('副标题')+'：'">
                                <el-checkbox v-model="promotion.isSubTitle" true-label="1"
                                             false-label="0"></el-checkbox>
                                <div class="poster-tit">
                                    <el-input v-model="promotion.extSubTitle" :placeholder="promotion.extSubTitle"
                                              width="300"></el-input>
                                </div>
                                <div class="poster-color">
                                    <el-color-picker v-model="promotion.extSubTColor"></el-color-picker>
                                </div>
                                <div class="poster-size">
                                    <el-select v-model="promotion.extSubTSize">
                                        <el-option
                                                v-for="item in options"
                                                :key="item.value"
                                                :label="item.label"
                                                :value="item.value">
                                        </el-option>
                                    </el-select>
                                </div>
                                <div class="poster-fam">
                                    <el-select v-model="promotion.extSubTFont">
                                        <el-option
                                                v-for="item in optionsFam"
                                                :key="item.value"
                                                :label="item.label"
                                                :value="item.value">
                                        </el-option>
                                    </el-select>
                                </div>
                            </el-form-item>
                            <el-form-item :label="$L('提示文本1')+'：'">
                                <el-checkbox v-model="promotion.isTip1" true-label="1" false-label="0"></el-checkbox>
                                <div class="poster-tit">
                                    <el-input v-model="promotion.extTip1" :placeholder="promotion.extTip1"
                                              width="300"></el-input>
                                </div>
                                <div class="poster-color">
                                    <el-color-picker v-model="promotion.extTColor1"></el-color-picker>
                                </div>
                                <div class="poster-size">
                                    <el-select v-model="promotion.extTSize1">
                                        <el-option
                                                v-for="item in options"
                                                :key="item.value"
                                                :label="item.label"
                                                :value="item.value">
                                        </el-option>
                                    </el-select>
                                </div>
                                <div class="poster-fam">
                                    <el-select v-model="promotion.extTFont1">
                                        <el-option
                                                v-for="item in optionsFam"
                                                :key="item.value"
                                                :label="item.label"
                                                :value="item.value">
                                        </el-option>
                                    </el-select>
                                </div>
                            </el-form-item>
                            <el-form-item :label="$L('提示文本2')+'：'">
                                <el-checkbox v-model="promotion.isTip2" true-label="1" false-label="0"></el-checkbox>
                                <div class="poster-tit">
                                    <el-input v-model="promotion.extTip2" :placeholder="promotion.extTip2"
                                              width="300"></el-input>
                                </div>
                                <div class="poster-color">
                                    <el-color-picker v-model="promotion.extTColor2"></el-color-picker>
                                </div>
                                <div class="poster-size">
                                    <el-select v-model="promotion.extTSize2">
                                        <el-option
                                                v-for="item in options"
                                                :key="item.value"
                                                :label="item.label"
                                                :value="item.value">
                                        </el-option>
                                    </el-select>
                                </div>
                                <div class="poster-fam">
                                    <el-select v-model="promotion.extTFont2">
                                        <el-option
                                                v-for="item in optionsFam"
                                                :key="item.value"
                                                :label="item.label"
                                                :value="item.value">
                                        </el-option>
                                    </el-select>
                                </div>
                            </el-form-item>
                            <div class="more" v-if="promotion.pid == 3">
                                <el-form-item :label="$L(' 优势标题')+'：'">
                                    <el-checkbox v-model="promotion.isAdvTitle" true-label="1"
                                                 false-label="0"></el-checkbox>
                                    <div class="poster-tit2">
                                        <el-input v-model="promotion.advTitle" :placeholder="promotion.advTitle"
                                                  width="300"></el-input>
                                    </div>
                                    <div class="poster-color">
                                        <el-color-picker v-model="promotion.advTColor"></el-color-picker>
                                    </div>
                                </el-form-item>
                                <el-form-item :label="$L(' 优势说明') + (index+1) + '：'"
                                              v-for="(item,index) in promotion.advContent" :key="item.index">
                                    <div class="poster-tit1 plant1">
                                        <el-input v-model="promotion.advContent[index]" :placeholder="item"
                                                  width="300"></el-input>
                                    </div>
                                    <div class="poster-add" @click="handleAdd" v-if="index == 0">
                                        <i class="el-icon-plus i"></i>
                                    </div>
                                    <div class="poster-add" @click="handleDel(index)" v-else>
                                        <i class="el-icon-minus i"></i>
                                    </div>
                                    <div class="poster-color-one" v-if="index == 0">
                                        <el-color-picker v-model="promotion.advContentColor"></el-color-picker>
                                    </div>
                                </el-form-item>
                                <el-form-item :label="$L('描述说明')+'：'" class="tips">
                                    <el-checkbox v-model="promotion.isNoteContent" true-label="1"
                                                 false-label="0"></el-checkbox>
                                    <div class="poster-tit3">
                                        <el-input type="textarea" v-model="promotion.noteContent" rows="4"></el-input>
                                    </div>
                                </el-form-item>
                            </div>
                        </el-form>
                        <div class="bottom">
                            <el-button class="button" @click="downImg">{{ $L('下载海报') }}</el-button>
                            <el-button type="primary" class="button" @click="doSave">{{ $L('保存设置') }}</el-button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import DiyImage from "../../../../components/common/diyImage";
    import ShareImg from "../../../../components/distribution/shareImg";
    import defaultUserImg from "../../../../assets/img/common/default_user.jpg";
    import DownloadImg from "../../../../components/common/downloadImg";

    export default {
        name: "promotionSettings",
        components: {ShareImg, DiyImage},
        data() {
            return {
                loading: true,
                extendList: [],
                promotion: {},
                options: [
                    {
                        value: '12px',
                        label: '12px'
                    },
                    {
                        value: '14px',
                        label: '14px'
                    },
                    {
                        value: '16px',
                        label: '16px'
                    },
                    {
                        value: '18px',
                        label: '18px'
                    },
                    {
                        value: '20px',
                        label: '20px'
                    },
                    {
                        value: '24px',
                        label: '24px'
                    },
                    {
                        value: '28px',
                        label: '28px'
                    },
                    {
                        value: '30px',
                        label: '30px'
                    },
                    {
                        value: '32px',
                        label: '32px'
                    },
                    {
                        value: '34px',
                        label: '34px'
                    },
                    {
                        value: '36px',
                        label: '36px'
                    },
                    {
                        value: '48px',
                        label: '48px'
                    },
                    {
                        value: '60px',
                        label: '60px'
                    },
                    {
                        value: '70px',
                        label: '70px'
                    },
                ],
                optionsFam: [
                    {
                        value: '微软雅黑',
                        label: '微软雅黑'
                    },
                    {
                        value: '宋体',
                        label: '宋体'
                    },
                    {
                        value: '新宋体',
                        label: '新宋体'
                    },
                    {
                        value: '仿宋_GB2312',
                        label: '仿宋_GB2312'
                    },
                    {
                        value: '楷体_GB2312',
                        label: '楷体_GB2312'
                    },
                    {
                        value: '黑体',
                        label: '黑体'
                    },
                    {
                        value: 'Arial',
                        label: 'Arial'
                    },
                    {
                        value: 'Arial Black',
                        label: 'Arial Black'
                    },
                    {
                        value: 'Times New Roman',
                        label: 'Times New Roman'
                    },
                    {
                        value: 'Courier New',
                        label: 'Courier New'
                    },
                    {
                        value: 'Tahoma',
                        label: 'Tahoma'
                    },
                    {
                        value: 'Verdana',
                        label: 'Verdana'
                    }
                ]
            }
        },
        created() {
            this.getData();
        },
        methods: {
            getData(id) {
                const _self = this;
                _self.loading = true;
                setTimeout(() => {
                    _self.$getApi('distributionShopSeoSetting', {id: id}, function (reData) {
                        _self.extendList = reData.extendList;
                        _self.promotion = reData.promotion;
                        _self.changeTemplateUrl(()=>{
                            _self.loading = false;
                        });

                    }, function (error) {
                        console.log(error);
                        _self.$confirm(_self.$L('网络请求失败'), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData(id);
                        }).catch(action => {
                            _self.$router.push({name: 'home'});
                        });
                    });
                }, 300);
            },
            changeTemplateUrl(func) {
                let c = 4;
                let d = 0;
                let doFunc = () => {
                    d++;
                    if (d === c && typeof func === "function") {
                        func();
                    }
                };
                if (this.promotion.hasOwnProperty('bgImage')) {
                    this.$getBase64(this.promotion.bgImage, (data) => {
                        this.promotion.bgImage = data;
                        doFunc();
                    });
                } else {
                    doFunc();
                }
                if (this.promotion.hasOwnProperty('userHead')) {
                    if (!this.promotion.userHead) {
                        this.promotion.userHead = defaultUserImg;
                    }
                    this.$getBase64(this.promotion.userHead, (data) => {
                        this.promotion.userHead = data;
                        doFunc();
                    });
                } else {
                    doFunc();
                }
                if (this.promotion.hasOwnProperty('shopHead')) {
                    this.$getBase64(this.promotion.shopHead, (data) => {
                        this.promotion.shopHead = data;
                        doFunc();
                    });
                } else {
                    doFunc();
                }
                if (this.promotion.hasOwnProperty('qrCode')) {
                    this.$getBase64(this.promotion.qrCode, (data) => {
                        this.promotion.qrCode = data;
                        doFunc();
                    });
                } else {
                    doFunc();
                }
            },
            defaultImg() {
                if (this.promotion && this.promotion.hasOwnProperty('defaultBgImage')) {
                    this.promotion.bgImage = this.promotion.defaultBgImage;
                }
            },
            upImgFunc(event) {
                const _self = this;
                let files = event.target.files[0];
                if (files) {
                    let size = Math.floor(files.size / 1024);
                    let type = files.type.substring(6);
                    if (size > 2 * 1024 * 1024) {
                        _self.$message({
                            message: _self.$L('请选择2M以内的图片！'),
                            type: 'warning'
                        });
                        return false;
                    }
                    if (!(type === 'png' || type === 'jpeg' || type === 'jpg')) {
                        _self.$message({
                            message: _self.$L('请选择png、jpeg、jpg的图片格式！'),
                            type: 'warning'
                        });
                        return false;
                    }
                    let reader = new FileReader();
                    reader.readAsDataURL(files);
                    reader.onload = function (evt) {
                        _self.promotion.bgImage = evt.target.result;
                    }
                }
            },
            downImg() {
                this.loading = true;
                setTimeout(() => {
                    this.$makeImg(this.$refs.showBox, {
                        backgroundColor: null
                    }).then((canvas) => {
                        this.$store.commit('setDialogObj', {
                            name: this.$L('右键图片可保存'),
                            view: DownloadImg,
                            width:'340px',
                            data:{
                                img:canvas.toDataURL()
                            }
                        });
                        this.loading = false;
                    });
                }, 500);
            },
            doSave() {
                this.loading = true;
                setTimeout(() => {
                    this.$makeImg(this.$refs.showBox, {
                        backgroundColor: null
                    }).then((canvas) => {
                        this.promotion.extendImg = canvas.toDataURL();
                        this.updateData();
                    });
                }, 500);
            },
            updateData() {
                const _self = this;
                _self.loading = true;
                setTimeout(() => {
                    _self.$getApi('shopSeoSettingSave', {promotion: _self.promotion}, function (reData) {
                        _self.extendList = reData.extendList;
                        _self.promotion = reData.promotion;
                        _self.changeTemplateUrl(()=>{
                            _self.$message({
                                message: _self.$L('操作成功'),
                                type: 'success'
                            });
                            _self.loading = false;
                        });
                    }, function (error) {
                        console.log(error);
                        _self.$confirm(_self.$L('网络请求失败'), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.updateData();
                        }).catch(action => {
                            _self.$router.push({name: 'home'});
                        });
                    });
                }, 300);
            },
            handleAdd() {
                this.promotion.advContent.splice(this.promotion.advContent.length, 0,
                    this.$L(' 优势说明')
                );
            },
            handleDel(index) {
                this.promotion.advContent.splice(index, 1);
            },
        }
    }
</script>

<style lang="scss" scoped>
    @import "../../../../assets/css/common/common";
    @import "../../../../assets/css/common/lib";
    @import "../../../../assets/css/theme/common.scss";

    .promotion-settings {
        position: relative;
        width: 100%;
        height: 100%;
        background: #fff;

        .content-view {
            width: 100%;

            .promotion-left {
                width: 250px;
                margin: 20px 0 20px 20px;
                display: inline-block;
                vertical-align: top;
                background: #f8f8f9;

                .system-header {
                    background: #f0f1f5;
                    font-weight: 700;
                    line-height: 50px;
                    padding-left: 20px;
                }

                .currentClick {
                    background-color: #fff;
                }

                .item-box {
                    .ul {
                        .li {
                            width: 100%;
                            height: 100%;
                            overflow: hidden;
                            padding: 15px 0;

                            .model-ul {
                                position: relative;

                                .selected {
                                    position: absolute;
                                    width: 20px;
                                    height: 20px;
                                    line-height: 20px;
                                    overflow: hidden;
                                    border-radius: 20px;
                                    color: #ffffff;
                                    text-align: center;
                                    bottom: 15px;
                                    right: 40px;
                                    background-color: #67C23A;
                                    border: 1px solid rgba(255, 255, 255, 0.3);
                                    opacity: 0;

                                    &.active {
                                        opacity: 1;
                                    }
                                }

                                .num {
                                    font-size: 14px;
                                    color: #333;
                                    display: inline-block;
                                    width: 40px;
                                    text-align: center;
                                    vertical-align: top;
                                }

                                .img-item {
                                    display: inline-block;
                                    width: calc(250px - 70px);
                                    cursor: pointer;
                                    min-height: 300px;
                                    position: relative;
                                    /deep/ .el-image__inner{
                                        width: 100%;
                                        height: 100%;
                                    }
                                }

                                .icon {
                                    display: inline-block;
                                    width: 50px;
                                    vertical-align: top;

                                    .span {
                                        font-size: 14px;
                                        display: block;
                                        cursor: pointer;
                                        color: #2d8cf0;
                                        background: #fff;
                                        padding: 2px;
                                        margin-left: 10px;
                                        width: 40px;
                                        text-align: center;
                                    }

                                    & > .span:first-child {
                                        margin-bottom: 5px;
                                    }
                                }
                            }
                        }
                    }
                }
            }

            .promotion-right {
                width: calc(100% - 250px - 60px);
                display: inline-block;
                vertical-align: top;
                background: #f0f1f5;
                margin: 20px;

                .promotion-tit {
                    font-weight: 700;
                    line-height: 50px;
                    padding-left: 20px;
                }

                .info-main {
                    background: #f8f8f9;
                    padding: 20px;

                    .info-left {
                        width: 300px;
                        display: inline-block;
                    }

                    .info-right {
                        width: calc(100% - 300px);
                        display: inline-block;
                        vertical-align: top;
                        padding-left: 5px;

                        /deep/ .el-checkbox {
                            margin-right: 20px;
                        }

                        .reset-btn {
                            display: inline-block;
                            vertical-align: top !important;

                            .label {
                                position: relative;
                                margin-right: 10px;
                                cursor: pointer;

                                .file-input {
                                    position: absolute;
                                    left: 0;
                                    opacity: 0;
                                    width: 80px;
                                    height: 32px;
                                    cursor: pointer;
                                }
                            }
                        }

                        .bg-img {
                            width: 65px;
                            height: 65px;
                            border: 1px solid #999;
                            display: inline-block;
                            /deep/ .el-image__inner{
                                width: 100%;
                                height: 100%;
                            }
                        }
                        .bg-img:hover{
                            opacity: 0.8;
                        }

                        .load-right {
                            display: inline-block;
                            vertical-align: top;
                            width: calc(100% - 100px);
                            .reset-btn {
                                padding-left: 10px;
                            }

                            .tips {
                                font-size: 12px;
                                padding-left: 10px;
                                color: #a39e9c;
                                overflow: hidden;
                                white-space: nowrap;
                                text-overflow: ellipsis;
                            }
                        }

                        .poster-name {
                            width: 300px;
                        }

                        .poster-tit {
                            display: inline-block;
                            width: 45%;
                        }

                        .poster-tit2 {
                            width: 66%;
                            display: inline-block;
                        }

                        .poster-tit1 {
                            margin-left: 31px;
                            display: inline-block;
                            width: 70%;

                            &.plant1 {
                                width: 66%;
                            }

                            &.plant2 {
                                width: 66%;
                            }
                        }

                        .poster-tit3 {
                            display: inline-block;
                            width: 66%;
                        }

                        .poster-add {
                            display: inline-block;
                            margin-left: 10px;
                            vertical-align: baseline;
                            width: 32px;
                            height: 32px;
                            background: #eee;
                            text-align: center;
                            border-radius: 5px;
                            cursor: pointer;

                            .i {
                                height: 32px;
                                line-height: 32px;
                            }
                        }

                        .poster-color {
                            display: inline-block;
                            padding-left: 10px;
                            vertical-align: middle;
                            height: 32px;
                        }
                        .poster-color-one {
                            display: inline-block;
                            padding-left: 10px;
                            vertical-align: middle;
                            height: 32px;
                        }
                        .poster-size {
                            display: inline-block;
                            padding-left: 10px;
                            width: 80px;
                        }

                        .poster-fam {
                            display: inline-block;
                            padding-left: 10px;
                            width: 90px;
                        }

                        .more {
                            .tips {
                                /deep/ .el-checkbox {
                                    vertical-align: top;
                                }
                            }
                        }
